import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setActionSingleConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve({
            legend: {},
            tooltip: {},
            title: {
                text: '用户个人基本属性',
                top: '90%',
                left: 'center',

            },
            dataset: {
                source: [
                    ['product', '2012', '2013', '2014', '2015', '2016', '2017'],
                    ['年龄', 86.5, 92.1, 85.7, 83.1, 73.4, 55.1],
                    ['性别', 41.1, 30.4, 65.1, 53.3, 83.8, 98.7],
                    ['学历', 24.1, 67.2, 79.5, 86.4, 65.2, 82.5],
                    ['购买时间段', 55.2, 67.1, 69.2, 72.4, 53.9, 39.1]
                ]
            },
            series: [{
                    type: 'pie',
                    radius: '20%',
                    center: ['25%', '30%']
                    // No encode specified, by default, it is '2012'.
                },
                {
                    type: 'pie',
                    radius: '20%',
                    center: ['75%', '30%'],
                    encode: {
                        itemName: 'product',
                        value: '2013'
                    }
                },
                {
                    type: 'pie',
                    radius: '20%',
                    center: ['25%', '75%'],
                    encode: {
                        itemName: 'product',
                        value: '2014'
                    }
                },
                {
                    type: 'pie',
                    radius: '20%',
                    center: ['75%', '75%'],
                    encode: {
                        itemName: 'product',
                        value: '2015'
                    }
                }
            ]


        })
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setActionSingleConfig