import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserBuyConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 模拟的用户数据
        var userData = [{
                name: '用户A',
                orders: 10,
                views: 50
            },
            {
                name: '用户B',
                orders: 8,
                views: 40
            },
            {
                name: '用户C',
                orders: 15,
                views: 30
            },
            {
                name: '用户D',
                orders: 5,
                views: 60
            },
            {
                name: '用户E',
                orders: 12,
                views: 45
            }
        ];

        // 根据下单数进行排序
        var orderByOrders = userData.slice().sort(function (a, b) {
            return b.orders - a.orders;
        });

        // 根据浏览数进行排序
        var orderByViews = userData.slice().sort(function (a, b) {
            return b.views - a.views;
        });



        // 获取用户名称数组
        function getUserNames(users) {
            return users.map(function (user) {
                return user.name;
            });
        }

        // 获取用户下单数数组
        function getUserOrders(users) {
            return users.map(function (user) {
                return user.orders;
            });
        }

        // 获取用户浏览数数组
        function getUserViews(users) {
            return users.map(function (user) {
                return user.views;
            });
        }



        // 配置图表选项
        var option = {
            title: {
                text: '电商用户购买行为统计',
                left: 'center'
            },
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'shadow'
                }
            },
            legend: {
                data: ['下单数', '浏览数'],
                top: 'bottom'
            },
            xAxis: {
                type: 'category',
                data: getUserNames(orderByOrders)
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                    name: '下单数',
                    type: 'bar',
                    data: getUserOrders(orderByOrders)
                },
                {
                    name: '浏览数',
                    type: 'bar',
                    data: getUserViews(orderByViews)
                }
            ]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserBuyConfig