import {
  createRouter,
  createWebHashHistory
} from 'vue-router'

import Layout from '../layout/Layout'
import purchaseSituational from '../views/purchaseSituation.vue'
import Yonghu from '@/views/Yonghu'
import zhuanHua from '../views/zhuanHua.vue'
import GouMaiAction from '../views/GouMaiAction.vue'
import ShangPinfenbu from '../views/ShangPinfenbu.vue'
import rfmModuleVue from '@/views/userValueViews/rfmModule.vue'
import clvModuleVue from '@/views/clvValueViews/clvModule.vue'
import purchaseSituationPersonal from '@/views/purchaseSituationPersonal'
const routes = [{
    path: '/',
    name: 'Layout',
    component: Layout,
    redirect: '/purchaseSituation',
    children: [ // 子路由
      {
        path: '/purchaseSituation',
        name: 'purchaseSituation',
        component: purchaseSituational
      },
    ]
  },
  {
    path: '/purchaseSituation',
    name: 'purchaseSituation',
    component: purchaseSituational,

  },
  {
    path:'/purchaseSituationPersonal',
    name:'purchaseSituationPersonal',
    component:purchaseSituationPersonal
  },
  {
    path: '/b',
    name: 'b',
    component: GouMaiAction,

  },
  {
    path: '/c',
    name: 'c',
    component: ShangPinfenbu,

  },
  {
    path: '/d',
    name: 'd',
    component: Layout,

  },
  {
    path: '/e',
    name: 'e',
    component: zhuanHua,

  },
  {
    path: '/f',
    name: 'f',
    component: rfmModuleVue,

  },
  {
    path: '/g',
    name: 'g',
    component: clvModuleVue,

  },
  {
    path: '/h',
    name: 'h',
    component: Yonghu,

  },
  {
    path: '/i',
    name: 'i',
    component: Layout,

  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router