import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'
import * as echarts from 'echarts'

export function setUserDrawConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 假设有以下用户画像树状数据
        var userData = {
            name: '用户画像',

            children: [{
                    name: '年龄',
                    children: [{
                            name: '18-24岁',
                            value: 20
                        },
                        {
                            name: '25-34岁',
                            value: 35
                        },
                        {
                            name: '35-44岁',
                            value: 15
                        },
                        {
                            name: '45岁以上',
                            value: 10
                        }
                    ]
                },
                {
                    name: '性别',
                    children: [{
                            name: '男性',
                            value: 60
                        },
                        {
                            name: '女性',
                            value: 40
                        }
                    ]
                },
                {
                    name: '地区',
                    children: [{
                            name: '华北地区',
                            value: 30
                        },
                        {
                            name: '华东地区',
                            value: 25
                        },
                        {
                            name: '华南地区',
                            value: 20
                        },
                        {
                            name: '西南地区',
                            value: 15
                        },
                        {
                            name: '西北地区',
                            value: 10
                        }
                    ]
                },
                {
                    name: '兴趣',
                    children: [{
                            name: '音乐',
                            value: 30
                        },
                        {
                            name: '电影',
                            value: 25
                        },
                        {
                            name: '运动',
                            value: 20
                        },
                        {
                            name: '旅游',
                            value: 15
                        },
                        {
                            name: '美食',
                            value: 10
                        }
                    ]
                },
                {
                    name: '消费偏好',
                    children: [{
                            name: '高端消费',
                            value: 30
                        },
                        {
                            name: '中端消费',
                            value: 25
                        },
                        {
                            name: '低端消费',
                            value: 20
                        },
                        {
                            name: '无消费偏好',
                            value: 25
                        }
                    ]
                }
            ]
        };


        // 配置图表选项
        var option = {
            tooltip: {
                trigger: 'item',
                triggerOn: 'mousemove'
            },
            series: [{
                type: 'tree',
                data: [userData],
                top: '1%',
                left: '7%',
                bottom: '1%',
                right: '20%',
                symbolSize: 10,
                label: {
                    position: 'left',
                    verticalAlign: 'middle',
                    align: 'right',
                    fontSize: 14
                },
                leaves: {
                    label: {
                        position: 'right',
                        verticalAlign: 'middle',
                        align: 'left'
                    }
                },
                expandAndCollapse: true,
                animationDuration: 550,
                animationDurationUpdate: 750
            }]
        };


        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setUserDrawConfig