import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'
import * as echarts from 'echarts'

export function setfrmDataConfig(params1, parmas2) {
    // 根据params，请求获取渲染图标的数据，静态数据调试把他注释了
    return new Promise((resolve, reject) => {
        // 定义数据
        const data = [{
                name: '数据点1',
                value: [10, 8]
            },
            {
                name: '数据点2',
                value: [-5, 12]
            },
            {
                name: '数据点3',
                value: [-15, -6]
            },
            {
                name: '数据点4',
                value: [8, -10]
            },
            // 添加更多数据点...
        ];

        // 配置选项
        const option = {
            title: {
                text: '四象限气泡图',
                x: 'center',
                textStyle: {
                    fontWeight: 'bold',
                    fontSize: 16,
                },
            },
            grid: {
                left: '10%',
                right: '10%',
                top: '15%',
                bottom: '10%',
                containLabel: true,
            },
            xAxis: {
                type: 'value',
                name: 'X轴',
                scale: true,
                axisLabel: {
                    formatter: '{value}',
                },
            },
            yAxis: {
                type: 'value',
                name: 'Y轴',
                scale: true,
                axisLabel: {
                    formatter: '{value}',
                },
            },
            series: [{
                type: 'scatter',
                data: data,
                symbolSize: function (data) {
                    return Math.sqrt(data[2]) * 10; // 根据第三个值设置气泡大小
                },
                label: {
                    show: true,
                    formatter: function (param) {
                        return param.data.name; // 设置气泡的标签显示数据点的名称
                    },
                    position: 'top',
                },
                itemStyle: {
                    color: '#428bca', // 设置气泡的颜色
                },
            }, ],
        };





        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data为可视化数据
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setfrmDataConfig